@use 'base/variables' as v;
@use 'base/mixins' as m;

.paginacion{
    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    

    @include m.tablet{
        justify-content: flex-end;
    }
    &__enlace{
        color: v.$azul;
        font-size: 2rem;
        background-color: v.$blanco;
        padding: 1rem;
        gap: .5rem;
        border-radius: .5rem;
        &:hover{
            transition-duration: 300ms;
            color: v.$blanco;
            background-color: v.$azul;
        }
        &--numero{
            display: none;
            @include m.tablet{
                display: inline;
            }
        }
        &--actual{
            background-color: v.$azul;
            color: v.$blanco;
            display: none;
            @include m.tablet{
                display: inline;
            }
        }
    }
}
